import React from 'react'
import { Link } from 'react-router-dom'

export const PerfilCard = () => {
    return (
        <div>
            <div className="card ms-3" style={{maxWidth: 540}}>
            <div className="row no-gutters">
                <div className="col-md-4">
                    <img src={`./assets/heroes/dc-arrow.jpg`} className="card-img" alt="Arrow" />
                </div>
                <div className="col-md-8">
                    <div className="card-body">
                        <h5 className="card-title">DC Arrow</h5>
                        <p className="card-text">Este es un personaje de DC</p>
                        <p className="card-text">
                            <small className="text-muted"> 2021 </small>
                        </p>

                        <Link to={`/PerfilScreen`}>
                            Mas...
                        </Link>
                        
                    </div>
                </div>
            </div>
        </div>
        </div>
    )
}
