import React from 'react'

export const PerfilScreen = ({history}) => {

    const handleReturn = () => {
        history.goBack();
    }
    return (
        
        <div className="row mt-5">
            <div className="col-4">
                <img
                src={`../assets/heroes/dc-arrow.jpg`}
                alt="DC Arrow"
                className="img-thumbnail animate__animated animate__fadeInLeft"
                />
            </div>

            <div className="col-8 animate__animated animate__fadeInRight">
                <h3> Flecha verde </h3>
                <ul className="list-group list-group-flush">
                    <li className="list-group-item"> <b>Alter ego: </b> Oliver Queen</li>
                    <li className="list-group-item"> <b>Publisher: </b> DC Comics</li>
                    <li className="list-group-item"> <b>First appearance: </b> More fun Comics #73</li>
                </ul>

                <h5>Characters</h5>
                <p> Olvier Queen </p>

                <button 
                    className="btn btn-outline-info"
                    onClick={handleReturn}
                >
                    Return
                </button>
            </div>
        </div>
    )
}
